import { ref } from "vue";
import { useIntersectionObserver } from "@vueuse/core";

export default function useLazyData(apiFunction) {
  // 
  const target = ref();
  // 
  const result = ref();

  const { stop } = useIntersectionObserver(
    target,
    ([{ isIntersecting }]) => {
      // 
      if (isIntersecting) {
        stop();
        // 
        apiFunction().then((data) => {
          console.log(data);
          result.value = data.result;
        });
      }
    },
    { threshold: 0 }
  );

  return { target, result };
}
