// 
import { ref } from "vue";
import { getBanners } from "@/api/home";

export default function useBanners() {
  // 
  const bannersData = ref();
  const getData = () => {
    // 
    getBanners().then((data) => {
      bannersData.value = data.result;
    });
  };
  return { bannersData, getData };
}
