const user = {
  namespaced: true,
  // 
  // vuex
  // 
  state() {
    return {
      // 
      profile: {
        // id
        id: "",
        // 
        avatar: "",
        // 
        nickname: "",
        // 
        account: "",
        // 
        mobile: "",
        // 
        token: "",
      },
    };
  },

  mutations: {
    setUser(state, user) {
      if (Object.keys(user).length > 0) {
        state.profile = { ...state.profile, ...user };
      } else {
        state.profile = {};
      }
    },
  },
};

export default user;
