import defaultImage from "@/assets/images/200.png";
const lazy = {
  // 
  mounted(el, binding) {
    // el 
    // binding 
    const observer = new IntersectionObserver(([{ isIntersecting }]) => {
      // 
      if (isIntersecting) {
        // 
        observer.unobserve(el);
        // 
        el.src = binding.value;
        // 
        el.onerror = function () {
          console.log("");
          el.src = defaultImage;
        };
      }
    });
    // 
    observer.observe(el);
  },
};

export default lazy;
